.gallery_list {
    display: flex;
    flex-flow: column nowrap;

    width: 80%;
    justify-content: space-evenly;
    padding: .3rem;
    box-sizing: border-box;
    border-radius: 0.5rem;
    margin: 1vh 0;
    overflow-y: auto;
    max-height: 65vh;
}

.gallery_list .img_list_container {
    width: 100%;
}